CheckLists = new Mongo.Collection('Checklists');

CheckLists.allow( {
		insert: function (userId) {return !! userId; },
		update: function (userId) {return !!userId; },
    remove: function (userId) {return !!userId; },
});

if (Meteor.isServer) {
	if (typeof CheckLists.createIndex === 'function') {
		CheckLists.createIndex({ full: "text" }, { name: "TextIndex" });
	}
	else {
		if (typeof CheckLists._ensureIndex === 'function') {
			CheckLists._ensureIndex( { full: "text" }, {name: "TextIndex"});
	}
}
} //end if Server

//expose it to the other packages :
RKCheckLists.CheckLists = CheckLists;
